.btn {
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    padding: 16px;
    background-color: $grey;
    color: white;
    font-size: 16px;
    line-height: 16px;
    border-radius: 4px;
    letter-spacing: 1px;
    transition: all .2s linear;
    display: inline-block;

    &:hover {
        background-color: $grey_light;
        color: $dark;
    }

    &_white {
        background-color: #fff;
        color: $dark;

        &:hover {
            background-color: $dark;
            color: #fff;
        }
    }
}